* {
  margin: auto;
}

h3 {
  text-align: center;
}

.user {
  display: flex;
  justify-content: center;
  padding: 12px;
  margin-top: 20px;
  font-size: 18px;
}
input {
  width: 80%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: vertical;
}
label {
  padding: 12px 12px 12px 0;
  display: inline-block;
}
.submit_btn {
  background: dodgerblue;
  color: white;
  margin: 10px 20px;
  width: 60%;
  height: 30px;
  border: none;
  border-radius: 4px;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 500;
}
